import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  padding: 37px 64px;
`;
export const HeadBox = styled.div`
  display: flex;
  width: 100%;
  h1 {
    font-size: 24px;
    font-weight: 500;
  }
  justify-content: space-between;
`;
export const UserAvatar = styled.div`
  display: flex;
  margin-top: 60px;
  #editAvatar {
    ::after {
      content: "";
      width: 90px;
      position: absolute;
      top: 0;
      border-radius: 50%;
      left: 0;
      height: 90px;
      background: #00000099;
    }
  }
`;
export const EditAvatarInput = styled.div`
  #edit-avatar {
    display: none;
  }
`;
export const UserName = styled.h4`
  font-size: 22px;
  margin-left: 22px;
  margin-top: 15px;
  font-weight: 500;
`;
export const AvatarImg = styled.div`
  position: relative;
  img[alt="avatar"] {
    width: 90px;
    height: 90px;
    object-fit: cover;
    border-radius: 100px;
    margin-bottom: 16px;
  }
  img[alt="editIcon"] {
    width: 26px;
    position: absolute;
    bottom: 15px;
    right: 0;
    height: 26px;
    cursor: pointer;
    padding: 5px;
    border-radius: 50%;
    background-color: #2993ff;
  }
`;
export const StaffInfo = styled.div``;
export const TopInfo = styled.div`
  #m-0 {
    margin: 0;
  }
  h1 {
    color: #00000020;
    font-size: 13px;
    font-weight: 400;
  }
  .MuiAutocomplete-hasPopupIcon.MuiAutocomplete-hasClearIcon.css-viou3o-MuiAutocomplete-root
    .MuiOutlinedInput-root {
    background: none;
    border: 1px solid #0000004d;
    padding: 8px;
    padding-right: 30px;
  }
  display: grid;
  grid-template-columns: 1fr 1fr 0.5fr;
  grid-gap: 40px;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
  width: 100%;
`;
export const Text = styled.p`
  font-size: 15px;
  font-weight: 400;
  margin-left: 10px;
`;
export const Title = styled.h4`
  font-size: 13px;
  font-weight: 400;
  color: #00000020;
`;
export const ReadOnlyInput = styled.div``;
export const InfoInput = styled.div`
  padding: 17px 20px;
  border: 1px solid #0000004d;
  border-radius: 7px;
  display: flex;
  align-items: center;
  margin-top: 10px;
  .css-dmmspl-MuiFormGroup-root {
    flex-direction: row;
    width: 100%;
    justify-content: space-around;
  }
`;
export const StaffTasks = styled.div`
  display: flex;
  max-width: 305px;
  justify-content: space-between;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 45px;
  box-shadow: 0px 5px 11px 0px #0000000f;
`;
export const TaskType = styled.div`
  padding: 16px 25px;
  max-width: 107px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  :nth-last-child(2) {
    border-right: 1px solid #00000010;
    border-left: 1px solid #00000010;
  }
  flex-direction: column;
  h2 {
    font-size: 32px;
    font-weight: 700;
    margin-top: 7px;
  }
  h4 {
    font-size: 12px;
    font-weight: 400;
  }
`;

export const StaffTasksBox = styled.div`
  margin-top: 40px;
  h1 {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 10px;
  }
`;
export const BackProfile = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
`;
export const BackButton = styled.button`
  background: #0496ff;
  border-radius: 25px;
  color: #fff;
  margin-right: 35px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px 20px;
  border: none;
  p {
    font-size: 15px;
    font-weight: 500;
    margin-left: 10px;
  }
`;
export const BoxForEdit = styled.div``;
export const EditStaffInfo = styled.div`
  max-width: 850px;
  width: 100%;
  button {
    border-radius: 25px;
    width: 100%;
  }
`;
export const FormSubmit = styled.div`
  max-width: 300px;
  width: 100%;
  margin-top: 50px;
`;
export const TopEditInputs = styled.div`
  margin-bottom: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  p {
    font-size: 13px;
    font-weight: 400;
    color: #00000020;
  }
  .css-wb57ya-MuiFormControl-root-MuiTextField-root {
    background: none;
    border: 1px solid #0000004d;
  }
`;

export const BottomEditInputs = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
  width: 100%;
  #editInput {
    padding: 7px 20px;
    display: flex;
    justify-content: center;
  }
`;
export const EditInput = styled.div`
  > div {
    margin: 0;
    margin-top: 10px;
  }
  .MuiTextField-root {
    background: none !important;
    /* border: 1px solid #0000004d !important; */
  }
  .css-o9k5xi-MuiInputBase-root-MuiOutlinedInput-root {
    border-radius: 7px;
  }
  .css-nxo287-MuiInputBase-input-MuiOutlinedInput-input {
    padding: 17.6px 14px;
  }
`;
